import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {Http} from "@angular/http";
import {LinkManagerProvider} from "../../providers/link-manager/link-manager";

/**
 * Generated class for the TosPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-tos',
  templateUrl: 'tos.html',
})
export class TosPage {

  private jsonContent = '';
  private isModal = false
  constructor(public navCtrl: NavController, public navParams: NavParams, private http:Http, private linkManager: LinkManagerProvider) {

      this.isModal = this.navParams.get("modal") != null;
      this.http.get(this.linkManager.getNutungsbedingungenAsHalJSON()).subscribe(response => {

          console.log(response)
          this.jsonContent = JSON.parse(response.text())["body"][0]["value"];
      })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TosPage');
  }

}



